        import React, { useEffect } from "react";
        import { Splide, SplideTrack } from "@splidejs/react-splide";
        import "../../../analytics-new/assets/css/slide.scss";

        const CreatedWalls = ({setactiveWall, section, splideRef, children}) => {

        useEffect(() => {

          if(section && setactiveWall){
                if (splideRef.current) {
                // Add event listener to track when the slide changes
                  splideRef.current.splide.on("move", (newIndex) => {
                    setactiveWall(newIndex)
                    console.log(newIndex,'setactiveWallsetactiveWall',section)
                });
              }
          }
        }, []);
        
        const settings = {
            start: 0,
            type: "loop",
            //rewind: false,
            speed: 1000,
            perMove: 1,
            loop: true,
            interval: 5000,
            drag: true,
            //clones: 1,
            perPage: section==1 ? 0 : 3,
            autoWidth: section==1 ? true : false,
            gap: section==1 ? 15 : -15,
            autoplay: false,
            pagination: false,
            arrows: true,
            fixedHeight: true,
            breakpoints: {
                0: {
                    perPage: 1
                },
                579: {
                    perPage: 2
                },
                767: {
                    perPage: 2,
                },
                1300: {
                    perPage: 2,
                },
                1600: {
                    perPage: 3,
                }
            }
        };
          

        return (
            <Splide ref={splideRef} hasTrack={false} options={settings}>
               <SplideTrack>{children}</SplideTrack>
            </Splide>
         );
       };

 export default React.memo(CreatedWalls);
